import { Header } from "@/components/header";
import { MainPage } from "@/components/main-page";

export default function Home() {
  return (
    <div className="flex flex-col min-h-screen bg-background text-foreground">
      <Header />
      <main className="flex-1 flex flex-col items-center justify-center p-4">
        <MainPage />
      </main>
       <footer className="text-center p-4 text-xs text-muted-foreground">
        <p>
          By submitting data above, you are agreeing to our Terms of Service and
          Privacy Notice, and to the sharing of your Sample submission with the
          security community. Please do not submit any personal information; we are
          not responsible for the contents of your submission. Learn more.
        </p>
        <p className="mt-2">
          Want to automate submissions? Check our API, or access your API key.
        </p>
      </footer>
    </div>
  );
}
